// eslint-disable-next-line
/* eslint-disable */
<template>
  <div class="user_info">

    <el-form :model="formData" status-icon :rules="rule" ref="ruleForm" label-width="120px" class="form">
      <el-form-item label="账号：">
        <span>{{ getAccount() }}</span>
      </el-form-item>

      <el-form-item label="原始密码：" prop="oldPwd">
        <el-input type="password" v-model="formData.oldPwd" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="新建密码：" prop="pwd">
        <el-input type="password" v-model="formData.pwd" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="确认密码：" prop="pwdAgain">
        <el-input type="password" v-model="formData.pwdAgain" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button class='submit' type="primary" @click="submitForm('ruleForm')">确定</el-button>
      </el-form-item>
    </el-form>

  </div>
</template>

<script>
  import { mapGetters } from 'vuex'

  const MIN_PWD_LENGTH = 3

  export default {
    name: 'changePwd',
    methods: {
      ...mapGetters('me', ['getAccount']),
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {

            if (this.formData.oldPwd.length < MIN_PWD_LENGTH ||
              this.formData.pwd.length < MIN_PWD_LENGTH ||
              this.formData.pwdAgain.length < MIN_PWD_LENGTH ) {
              alert(`密码太短，至少${MIN_PWD_LENGTH}位`)
              return false
            }

            if (this.formData.pwd !== this.formData.pwdAgain) {
              alert('两次密码不一致')
              return false
            }

            let params = {
              oldPassword: this.formData.oldPwd,
              newPassword: this.formData.pwd,
              newPassword2: this.formData.pwdAgain,
            }
            params = this.$qs.stringify(params)
            gapi.changePassword(params).then(res => {

              this.formData.oldPwd = ''
              this.formData.pwd = ''
              this.formData.pwdAgain = ''
              this.$refs['ruleForm'].resetFields()

              if (res.code === 1) {
                this.$alert('成功更改密码!', '提示', {
                  confirmButtonText: '确定',
                  center: true,
                  callback: action => {
                    this.formData.oldPwd = ''
                    this.formData.pwd = ''
                    this.formData.pwdAgain = ''

                    this.$refs['ruleForm'].resetFields()
                  }
                })
              } else {
                this.$alert(res.failure, '错误提示', {
                  confirmButtonText: '',
                  center: true,
                  type: 'error'
                })
              }
            }).catch((err) => {
              console.log(err)
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      resetForm (formName) {
        this.$refs[formName].resetFields()
      }
    },
    data: function () {
      let validateOldPwd = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('密码不能为空!'))
        } else {
          callback()
        }
      }

      let validatePwd = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('密码不能为空!'))
        } else {
          if (this.formData.pwdAgain !== '') {
            this.$refs.ruleForm.validateField('pwdAgain')
          }
          callback()
        }
      }

      let validatePwdAgain = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('密码不能为空'))
        } else if (value !== this.formData.pwd) {
          callback(new Error('两次输入密码不一致'))
        } else {
          callback()
        }
      }

      return {
        formData: {
          oldPwd: '',
          pwd: '',
          pwdAgain: ''
        },
        rule: {
          oldPwd: [
            { validator: validateOldPwd, trigger: 'blur' }
          ],
          pwd: [
            { validator: validatePwd, trigger: 'blur' }
          ],
          pwdAgain: [
            { validator: validatePwdAgain, trigger: 'blur' }
          ]
        }
      }

    },
    created () {
      gapi.getUsersCurrent()
        .then((res) => {
          console.log(res)
          if (res.data.code === 1) {

          }
        }).catch((err) => {
        console.log(err)
      })
    }
  }
</script>

<style scoped>
  .user_info {
    background: rgba(255,255,255,1);
    border-radius: 10px;
    box-shadow: 0 0 10px 2px rgba(6,0,1,0.1);
    padding: 40px 30px 50px;
  }

  #row {
    display: flex;
    flex-direction: row;
  }

  .user_info span div{
    padding: 10px 20px;
    font-size: 16px;
    font-family: MicrosoftYaHei;
    color: rgba(144,144,144,1);
    line-height: 20px;
  }

  .user_info  #col_right {
    color: #666666;
  }

  #col_left{
    text-align: right;
    margin-right: 20px;
    width: 130px;
  }
  #col_right{
    text-align: left;
    flex: 1;
    display: flex;
  }
  .info_phone_modify {
    color: #4778C7;
    cursor: pointer;
    margin-left: 16px;
  }

  .form{
    width:480px
  }

  .submit {
    width: 100px;
  }



</style>



